.cart{
  width: 500px;
  margin: 0 auto;
  margin-top: 15px;
  background-color: #000;
}
h1{
  text-align: center;
  background-color: #ff79bc;
}
.cart-product-list{
  width: 340px;
  margin: 0 auto;
}
.product-in-cart{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: #fff;
  padding: 20px;
}
.count-wrap{
  display: -ms-flexbox;
  display: flex;
  width: 100px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.count-wrap div{
  width: 24px;
  height: 24px;
  border: 2px solid #000;
  text-align: center;
  border-radius: 50%;
  background-color: red;
}



